.ToDoItem {
  display: grid;
  grid-template-columns: 1fr 50px 50px;
  margin-bottom: 15px;
  background: #e8e8e8;
  border-radius: 5px;
  padding-right: 10px;
  &Done {
    background-color: #fcf8f8f4;
    text-decoration: line-through;
  }

  &Label {
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: center;
    padding: 12px 15px;
  }

  &Checkbox {
    width: 20px;
    height: 20px;
    cursor: pointer;
  }
  &Title {
    padding: 5px;
    line-height: 1.2;
  }
  &Fix {
    border: none;
    outline: none;
    padding: 5px;
    background: none;
    box-shadow: 0 4px 0 -3px #789cc7;
  }

  &Edit,
  &Remove,
  &Save {
    display: block;
    border: none;
    background: none no-repeat center center;
    cursor: pointer;
    padding: 0;
    transition: opacity 0.4s;
    &:hover {
      opacity: 0.5;
    }
  }
  &Edit {
    background-image: url('./images/edit.svg');
  }
  &Remove {
    background-image: url('./images/trash.svg');
  }
  &Save {
    background-image: url('./images/check.svg');
  }
}
